button,
input[type="button"] {
   border: none;
   border-radius: 0;
   background: transparent;
   color: black;
   cursor: pointer;
   display: inline-block;
   padding: 1em 1.5em;
   text-transform: uppercase;
   transition: all 0.25s ease-in-out;
   position: relative;
   z-index: 8;
}

.nav-toggle {
   font-size: 0.875em;
   padding: 1em;
   background-color: transparent;
}

button:focus {
   outline: 0;
}

button:hover {
   color: #696868;
}

.icon {
   display: inline-block;
   fill: currentColor;
   height: 1em;
   width: 1em;
   vertical-align: middle;
   position: relative;
   top: -0.0625em;
   z-index: 8;
}

/* Menu toggle styles. */
.icon-nav-toggle {
   width: 2em;
   height: 2em;
   top: 0;
}

/* Animate menu icon (lines). */
.svg-nav-toggle .line {
   opacity: 1;
   -webkit-transform: rotate(0) translateY(0) translateX(0);
           transform: rotate(0) translateY(0) translateX(0);
   -webkit-transform-origin: 1em 1em;
           transform-origin: 1em 1em;
   transition: opacity 0.2s ease-in-out, -webkit-transform 0.3s ease-in-out;
   transition: transform 0.3s ease-in-out, opacity 0.2s ease-in-out;
   transition: transform 0.3s ease-in-out, opacity 0.2s ease-in-out, -webkit-transform 0.3s ease-in-out;
}

.svg-nav-toggle .line-1 {
   -webkit-transform-origin: 1em 2.5em;
           transform-origin: 1em 2.5em;
}

.svg-nav-toggle .line-3 {
   -webkit-transform-origin: 1em 4.5em;
           transform-origin: 1em 4.5em;
}

.nav-toggle.opened .svg-nav-toggle .line-1 {
   -webkit-transform: rotate(45deg) translateY(0) translateX(0);
           transform: rotate(45deg) translateY(0) translateX(0);
}

.nav-toggle.opened .svg-nav-toggle .line-2 {
   opacity: 0;
}

.nav-toggle.opened .svg-nav-toggle .line-3 {
   -webkit-transform: rotate(-45deg) translateY(0em) translateX(0em);
           transform: rotate(-45deg) translateY(0em) translateX(0em);
}

