@-webkit-keyframes pulse {
   0% {
      -webkit-transform: scale(0.8);
              transform: scale(0.8);
      opacity: 0.8;
   }
   70% {
      -webkit-transform: scale(1);
              transform: scale(1);
      opacity: 1;
   }
   100% {
      -webkit-transform: scale(0.8);
              transform: scale(0.8);
      opacity: 0.8;
   }
}

@keyframes pulse {
   0% {
      -webkit-transform: scale(0.8);
              transform: scale(0.8);
      opacity: 0.8;
   }
   70% {
      -webkit-transform: scale(1);
              transform: scale(1);
      opacity: 1;
   }
   100% {
      -webkit-transform: scale(0.8);
              transform: scale(0.8);
      opacity: 0.8;
   }
}

